@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,600,700,900);
* {
  outline: none !important;
}

body {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.account-root .account-container{flex:1 1;height:100vh;width:100%;display:flex;background-color:transparent;color:#ffffff;align-items:center;flex-direction:column;cursor:pointer;margin-left:0}.account-root .account-middle-txt{text-align:center;margin-top:140px;font-size:46px}.account-root .account-middle-txt:nth-child(2){font-size:38px;margin-top:10px}.account-root .open-btn{width:14vw;margin-top:20px;color:#ffffff;background-color:#F5B812}.account-root .open-btn:hover{background-color:#F5B812}@media (max-width: 760px){.account-root .account-container{margin-left:0}.account-root .open-btn{width:32vw}}

.home-root .card{flex:1 1;height:100vh;width:100%;display:flex;background-color:transparent;color:#ffffff;margin-left:170%;flex-direction:column;cursor:pointer}.home-root .home-middle-txt{text-align:center;margin-top:140px;font-size:46px}.home-root .home-middle-txt:nth-child(2){font-size:38px;margin-top:10px}.home-root .open-btn{margin-top:20px;color:#ffffff;background-color:#061B33}.home-root .open-btn:hover{background-color:#061B33}@media (min-width: 1600px){.home-root .card{margin-left:220%}}@media (max-width: 750px){.home-root .card{margin-left:0}}

